.root {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    text-align: center;
    font-family: system-ui, -apple-system, sans-serif;
  }
  
  /* Table Selection Styles */
  .table-options {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    margin: 2rem 0;
  }
  
  .table-option {
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #ddd;
    border-radius: 12px;
    cursor: pointer;
    font-size: 1.5rem;
    transition: all 0.2s ease;
  }
  
  .table-option:hover {
    background-color: #f0f0f0;
    transform: scale(1.05);
  }
  
  .selected-table {
    background-color: #4CAF50;
    color: white;
    border-color: #4CAF50;
  }
  
  .selected-table:hover {
    background-color: #45a049;
  }
  
  input[type="button"] {
    background-color: #2196F3;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 1.1rem;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  input[type="button"]:hover {
    background-color: #1976D2;
  }
  
  /* Trainer Styles */
  .trainer-container {
    max-width: 500px;
    margin: 0 auto;
    padding: 2rem;
  }
  
  .progress {
    font-size: 1.1rem;
    color: #666;
    margin-bottom: 2rem;
  }
  
  .question {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
    color: #333;
  }
  
  .answer-form {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
  }
  
  .answer-input {
    width: 120px;
    height: 48px;
    font-size: 1.5rem;
    text-align: center;
    border: 2px solid #ddd;
    border-radius: 8px;
    padding: 0.5rem;
  }
  
  .answer-input:focus {
    outline: none;
    border-color: #2196F3;
  }
  
  .submit-button {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 1.1rem;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .submit-button:hover {
    background-color: #45a049;
  }
  
  /* Complete Screen Styles */
  .complete-container {
    text-align: center;
    padding: 2rem;
  }
  
  .complete-container h2 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 1rem;
  }
  
  .complete-container p {
    font-size: 1.2rem;
    color: #666;
    margin: 0.5rem 0;
  }
  
  .reset-container{
    margin-top: 1.5rem;
  }
  .reset-container input{
    display: block;
    margin: auto;
    background-color: #2196F3;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 1.1rem;
    border-radius: 8px;
    cursor: pointer;
    margin-top: 10px;
    transition: background-color 0.2s;
  }
  
  .reset-container input:hover {
    background-color: #1976D2;
  }